<template>
  <div>
    <!---->
    <div class='contacTitleWarp'>
      <div class='contacTitle'></div>
      <div class='contacTitleButton'>
        <el-button type="info" size="mini" @click='haldAddEquipment'></el-button>
        <el-button type="danger" size="mini" @click='fastDeleteBth'></el-button>
      </div>
    </div>
    <!---->
    <div class='contacTinformation'>
      <el-table
        :data="equipmentList"
        border
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          type="index"
          align='center'
          width="50">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align='center'
          width="220">
        </el-table-column>
        <el-table-column
          prop="number"
          label=""
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="instrumentSituation"
          label=""
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="remark"
          label=""
          align='center'
          width="180">
        </el-table-column>
        <el-table-column
          align='center'
          label=""
          width="120">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small" @click='deleteBth(scope.row)'>
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="equipmentShow" top='40px'>
      <div class='addContactsWarp'>
        <el-form ref="addEquipmentList" :model="addEquipmentList" :rules='rules' label-width="80px" size='medium'
                 label-position='top'>
          <el-form-item label=":" prop='name'>
            <el-input v-model="addEquipmentList.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" prop='number'>
            <el-input v-model="addEquipmentList.number" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" label-width='120px' prop='instrumentSituation'>
            <el-input v-model="addEquipmentList.instrumentSituation" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" prop='remark'>
            <el-input
              type="textarea"
              :rows="4"
              placeholder=""
              v-model="addEquipmentList.remark"
              resize='none'
            >
            </el-input>
          </el-form-item>
        </el-form>
        <!---->
        <div class='defineWarp'>
          <el-button type="primary" size='medium' @click='additions'></el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  // 
  import {config} from 'api/config.js';
  //
  import axios from 'api/axios.js'
  // Vuex
  import {mapState} from 'Vuex'

  export default {
    name: 'equipmentList',
    props: {
      equipmentList: {
        type: Array
      }
    },
    data() {
      return {
        // 
        equipmentShow: false,
        // 
        multipleSelection: [],
        // 
        addEquipmentList: {
          // 
          name: '',
          // 
          number: '',
          // 
          instrumentSituation: '',
          // 
          remark: '',
        },
        // 
        rules: {
          name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          number: [
            {required: true, message: '', trigger: 'blur'},
          ],
          instrumentSituation: [
            {required: true, message: '', trigger: 'blur'},
          ],
          remark: [
            {required: true, message: '', trigger: 'blur'},
          ]
        }
      }
    },
    computed: {
      //  uuid
      ...mapState(['uuid']),
    },
    methods: {
      // 
      haldAddEquipment() {
        this.equipmentShow = true
        // 
        this.addEquipmentList.name = '',
        this.addEquipmentList.number = '',
        this.addEquipmentList.instrumentSituation = '',
        this.addEquipmentList.remark = ''
      },
      // 
      additions() {
        // 
        this.$refs['addEquipmentList'].validate((valid) => {
          if (valid) {
            const params = {
              supplierUuid: this.uuid,
              // 
              name: this.addEquipmentList.name,
              // 
              number: this.addEquipmentList.number,
              // 
              instrumentSituation: this.addEquipmentList.instrumentSituation,
              // 
              remark: this.addEquipmentList.remark
            }
            axios.post(config.api, '/supplier/addSupplierEquipment.do', params).then((res) => {
              console.log(res)
              if (res.status == 0) {
                this.$alert('', {
                  confirmButtonText: '',
                  center: true,
                  callback: action => {
                    // 
                    this.equipmentShow = false
                    // 
                    this.$emit('againLoad')
                  }
                })
              } else {
                this.$alert(res.msg, {
                  confirmButtonText: '',
                  center: true
                })
              }
            })
          }
        })
      },
      // 
      handleSelectionChange(val) {
        this.multipleSelection = []
        val.map((item) => {
          this.multipleSelection.push(item.uuid)
        })
      },
      // 
      fastDeleteBth() {
        const params = {
          tableName: "supplier_equipment",
          uuidList: this.multipleSelection
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if (res.status == 0) {
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      },
      // 
      deleteBth(scope) {
        const params = {
          tableName: "supplier_equipment",
          uuidList: Array(scope.uuid)
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if (res.status == 0) {
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      }
    }
  }
</script>

<style lang='stylus' scoped>
  .contacTitleWarp
    width 100%
    height 50px;
    margin-top 30px;
    background: #f5f5f5;
    position relative
    .contacTitle
      width 110px;
      line-height 50px;
      font-size 20px;
      text-align center
      position absolute
      left 50%;
      margin-left: -65px;
    .contacTitleButton
      position absolute
      right 100px;
      top: 11px;

  .contacTinformation
    width: 100%;
    margin 0px auto
    margin-bottom 10px;

  .addContactsWarp
    width 80%
    margin 0px auto
    .defineWarp
      width 100%
      height: 45px;
      margin-top 20px;
      margin-left 20px;
      display flex
      justify-content center
      align-items center
</style>



