<template>
  <div>
    <div class='contacTitleWarp'>
      <div class='contacTitle'></div>
      <div class='contacTitleButton'>
        <el-button type="info" size="mini" @click='haldAddproduct'></el-button>
        <el-button type="danger" size="mini" @click='fastDeleteBth'></el-button>
      </div>
    </div>
    <!---->
    <div class='contacTinformation'>
      <el-table
        :data="productList"
        border
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          type="index"
          align='center'
          width="50">
        </el-table-column>
        <el-table-column
          prop="productName"
          label=""
          align='center'
          width="220">
        </el-table-column>
        <el-table-column
          prop="normalDeliveryDate"
          label=""
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="minimumDeliveryDate"
          label=""
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="averageMonthMaxCount"
          label=""
          align='center'
          width="180">
        </el-table-column>
        <el-table-column
          align='center'
          label=""
          width="120">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small" @click='deleteBth(scope.row)'>
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="productsShow">
      <div class='addContactsWarp'>
        <el-form ref="addproductData" :model="addproductData" :rules='rules'  size='medium' label-width='140px'label-position='top' >
          <el-form-item label=" :" prop='productName'>
            <el-input v-model="addproductData.productName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=" :" prop='normalDeliveryDate'>
            <el-input v-model="addproductData.normalDeliveryDate" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=" :" prop='minimumDeliveryDate'>
            <el-input v-model="addproductData.minimumDeliveryDate" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=" :" prop='averageMonthMaxCount'>
            <el-input v-model="addproductData.averageMonthMaxCount" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <!---->
        <div class='defineWarp'>
          <el-button type="primary" size='medium' @click='additions'></el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  // 
  import {config} from 'api/config.js';
  //
  import axios from 'api/axios.js'
  // Vuex
  import {mapState} from 'Vuex'

  export default {
    name: 'bankAccount',
    props: {
      productList: {
        type: Array
      }
    },
    data() {
      return {
        // 
        productsShow:false,
        // 
        multipleSelection: [],
        // 
        addproductData:{
          // 
          productName:'',
          // 
          normalDeliveryDate:'',
          // 
          minimumDeliveryDate:'',
          // 
          averageMonthMaxCount:''
        },
        // 
        rules: {
          productName: [
            {required: true, message: '', trigger: 'blur'},
          ],
          normalDeliveryDate:[
            {required: true, message: '', trigger: 'blur'},
          ],
          minimumDeliveryDate:[
            {required: true, message: '', trigger: 'blur'},
          ],
          averageMonthMaxCount:[
            {required: true, message: '', trigger: 'blur'},
          ]
        }
      }
    },
    computed: {
      //  uuid
      ...mapState(['uuid']),
    },
    methods:{
      // 
      haldAddproduct(){
        this.productsShow=true
        // 
        this.addproductData.productName=''
        this.addproductData.normalDeliveryDate=''
        this.addproductData.minimumDeliveryDate=''
        this.addproductData.averageMonthMaxCount=''

      },
      // 
      handleSelectionChange(val){
        this.multipleSelection=[]
        val.map((item)=>{
          this.multipleSelection.push(item.uuid)
        })
      },
      // 
      additions() {
        // 
        this.$refs['addproductData'].validate((valid) => {
          if (valid) {
            const params = {
              supplierUuid: this.uuid,
              // 
              productName:this.addproductData.productName,
              // 
              normalDeliveryDate: this.addproductData.normalDeliveryDate,
              // 
              minimumDeliveryDate: this.addproductData.minimumDeliveryDate,
              // 
              averageMonthMaxCount: this.addproductData.averageMonthMaxCount,
            }
            axios.post(config.api, '/supplier/addSupplierProduct.do', params).then((res) => {
              if (res.status == 0) {
                this.$alert('', {
                  confirmButtonText: '',
                  center: true,
                  callback: action => {
                    // 
                    this.productsShow = false
                    // 
                    this.$emit('againLoad')
                  }
                })
              } else {
                this.$alert(res.msg, {
                  confirmButtonText: '',
                  center: true
                })
              }
            })
          }
        })
      },
      // 
      fastDeleteBth(){
        const params={
          tableName: "supplier_product",
          uuidList: this.multipleSelection
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if(res.status == 0){
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      },
      // 
      deleteBth(scope){
        const params={
          tableName: "supplier_product",
          uuidList: Array(scope.uuid)
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if(res.status == 0){
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      }
    }
  }
</script>

<style lang='stylus' scoped>
  .contacTitleWarp
    width 100%
    height 50px;
    margin-top 30px;
    background: #f5f5f5;
    position relative
    .contacTitle
      width 130px;
      line-height 50px;
      font-size 20px;
      text-align center
      position absolute
      left 50%;
      margin-left: -65px;
    .contacTitleButton
      position absolute
      right 100px;
      top: 11px;

  .contacTinformation
    width: 100%;
    margin 0px auto
    margin-bottom 10px;

  .addContactsWarp
    width 80%
    margin 0px auto
    .defineWarp
      width 100%
      height:45px;
      margin-top 20px;
      margin-left 20px;
      display flex
      justify-content center
      align-items center
</style>



