<template>
  <div>
    <div class='contacTitleWarp'>
      <div class='contacTitle'></div>
      <div class='contacTitleButton'>
        <el-button type="info" size="mini" @click='haldAddBank'></el-button>
        <el-button type="danger" size="mini" @click='fastDeleteBth'></el-button>
      </div>
    </div>
    <!---->
    <div class='contacTinformation'>
      <el-table
        :data="customerList"
        border
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          type="index"
          align='center'
          width="50">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align='center'
          width="300">
        </el-table-column>
        <el-table-column
          prop="saleRate"
          label=""
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="productionCapacityRate"
          label=""
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          align='center'
          label=""
          width="120">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small" @click='deleteBth(scope.row)'>
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="CustomerShow">
      <div class='addContactsWarp'>
        <el-form ref="addCustomerData" :model="addCustomerData" :rules='rules'  label-width="80px" size='medium' label-position='top'>
          <el-form-item label="" prop='name'>
            <el-input v-model="addCustomerData.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='saleRate'>
            <el-input v-model="addCustomerData.saleRate" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='productionCapacityRate'>
            <el-input v-model="addCustomerData.productionCapacityRate" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <!---->
        <div class='defineWarp'>
          <el-button type="primary" size='medium' @click='additions'></el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  // 
  import {config} from 'api/config.js';
  //
  import axios from 'api/axios.js'
  // Vuex
  import {mapState} from 'Vuex'

  export default {
    name: 'mainCustomer',
    props: {
      customerList: {
        type: Array
      }
    },
    data() {
      return {
        // 
        CustomerShow:false,
        // 
        multipleSelection: [],
        // 
        addCustomerData:{
          // 
          name:'',
          // 
          saleRate:'',
          // 
          productionCapacityRate:''
        },
        // 
        rules: {
          name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          saleRate:[
            {required: true, message: '', trigger: 'blur'},
          ],
          productionCapacityRate:[
            {required: true, message: '', trigger: 'blur'},
          ]
        }
      }
    },
    computed: {
      //  uuid
      ...mapState(['uuid']),
    },
    methods:{
      // 
      haldAddBank(){
        this.CustomerShow=true
        // 
        this.addCustomerData.name=''
        this.addCustomerData.saleRate=''
        this.addCustomerData.productionCapacityRate=''
      },
      // 
      additions() {
        // 
        this.$refs['addCustomerData'].validate((valid) => {
          if (valid) {
            const params = {
              supplierUuid: this.uuid,
              // 
              name:this.addCustomerData.name,
              // 
              saleRate: this.addCustomerData.saleRate,
              // 
              productionCapacityRate: this.addCustomerData.productionCapacityRate
            }

            axios.post(config.api, '/supplier/addSupplierCustomer.do', params).then((res) => {
              if (res.status == 0) {
                this.$alert('', {
                  confirmButtonText: '',
                  center: true,
                  callback: action => {
                    // 
                    this.CustomerShow = false
                    // 
                    this.$emit('againLoad')
                  }
                })
              } else {
                this.$alert(res.msg, {
                  confirmButtonText: '',
                  center: true
                })
              }
            })
          }
        })
      },
      // 
      handleSelectionChange(val){
        this.multipleSelection=[]
        val.map((item)=>{
          this.multipleSelection.push(item.uuid)
        })
      },
      // 
      fastDeleteBth(){
        const params={
          tableName: "supplier_customer",
          uuidList: this.multipleSelection
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if(res.status == 0){
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      },
      // 
      deleteBth(scope){
        const params={
          tableName: "supplier_customer",
          uuidList: Array(scope.uuid)
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if(res.status == 0){
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      }
    }
  }
</script>

<style lang='stylus' scoped>
  .contacTitleWarp
    width 100%
    height 50px;
    margin-top 30px;
    background: #f5f5f5;
    position relative
    .contacTitle
      width 130px;
      line-height 50px;
      font-size 20px;
      text-align center
      position absolute
      left 50%;
      margin-left: -53px;
    .contacTitleButton
      position absolute
      right 100px;
      top: 11px;

  .contacTinformation
    width: 100%;
    margin 0px auto
    margin-bottom 10px;

  .addContactsWarp
    width 80%
    margin 0px auto
    .defineWarp
      width 100%
      height:45px;
      margin-top 20px;
      margin-left 20px;
      display flex
      justify-content center
      align-items center
</style>



