<template>
  <div>
    <!---->
    <div class='contacTitleWarp'>
      <div class='contacTitle'></div>
      <div class='contacTitleButton'>
        <el-button type="info" size="mini" @click='haldAddContacts'></el-button>
        <el-button type="danger" size="mini" @click='fastDeleteBth'></el-button>
      </div>
    </div>
    <!---->
    <div class='contacTinformation'>
      <el-table
        :data="contractList"
        border
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          type="index"
          align='center'
          width="50">
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          align='center'
          width="150">
        </el-table-column>
        <el-table-column
          label=""
          align='center'
          width="120">
          <template slot-scope="scope">
            <span>{{scope.row.sex|filtersSex}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="post"
          label=""
          align='center'
          width="130">
        </el-table-column>
        <el-table-column
          prop="telephone"
          label=""
          align='center'
          width="160">
        </el-table-column>
        <el-table-column
          prop="phone"
          label=""
          align='center'
          width="120">
        </el-table-column>
        <el-table-column
          prop="remark"
          label=""
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          align='center'
          label=""
          width="120">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small" @click='deleteBth(scope.row)'>
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="addContacts">
      <div class='addContactsWarp'>
        <el-form ref="addContactsData" :rules='rules' :model="addContactsData" label-width="80px" size='medium'>
          <el-form-item label="" prop='name'>
            <el-input v-model="addContactsData.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='sex'>
            <el-radio-group v-model="addContactsData.sex">
              <el-radio label="1"></el-radio>
              <el-radio label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop='post'>
            <el-select v-model="addContactsData.post" placeholder="" style='width:100%'>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop='telephone'>
            <el-input v-model="addContactsData.telephone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='phone'>
            <el-input v-model="addContactsData.phone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='remark'>
            <el-input
              type="textarea"
              :rows="4"
              resize='none'
              placeholder=""
              v-model="addContactsData.remark">
            </el-input>
          </el-form-item>
        </el-form>
        <!---->
        <div class='defineWarp'>
          <el-button type="primary" size='medium' @click='additions'></el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  // 
  import {config} from 'api/config.js';
  //
  import axios from 'api/axios.js'
  // Vuex
  import {mapState} from 'Vuex'

  export default {
    name: 'contaclnformation',
    props: {
      contractList: {
        type: Array
      }
    },
    data() {
      return {
        // 
        addContacts: false,
        // 
        multipleSelection: [],
        // 
        addContactsData: {
          // 
          name: '',
          // 
          sex: '',
          // 
          post: '',
          // 
          telephone: '',
          // 
          phone: '',
          // 
          remark: ''
        },
        // 
        rules: {
          name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          sex: [
            {required: true, message: '', trigger: 'blur'},
          ],
          post: [
            {required: true, message: '', trigger: 'change'},
          ],
          telephone: [
            {required: false, message: '', trigger: 'blur'},
          ],
          phone: [
            {required: true, message: '', trigger: 'blur'},
          ],
          remark: [
            {required: false, message: '', trigger: 'blur'},
          ]
        }
      }
    },
    computed: {
      //  uuid
      ...mapState(['uuid']),
    },
    filters:{
      filtersSex(data){
        if(data==0){
          return ''
        }else if(data==1){
          return ''
        }
      }
    },
    methods: {
      // 
      haldAddContacts() {
        this.addContacts = true
        // 
        this.addContactsData.name = '',
        this.addContactsData.sex = '',
        this.addContactsData.post = '',
        this.addContactsData.telephone = '',
        this.addContactsData.phone = '',
        this.addContactsData.remark = ''
      },
      // 
      additions() {
        // 
        this.$refs['addContactsData'].validate((valid) => {
          if (valid) {
            const params = {
              supplierUuid: this.uuid,
              // 
              name: this.addContactsData.name,
              // 
              sex: this.addContactsData.sex,
              // 
              post: this.addContactsData.post,
              // 
              telephone: this.addContactsData.telephone,
              // 
              phone: this.addContactsData.phone,
              // 
              remark: this.addContactsData.remark
            }
            axios.post(config.api, '/supplier/addSupplierContract.do', params).then((res) => {
              if (res.status == 0) {
                this.$alert('', {
                  confirmButtonText: '',
                  center: true,
                  callback: action => {
                    // 
                    this.addContacts = false
                    // 
                    this.$emit('againLoad')
                  }
                })
              } else {
                this.$alert(res.msg, {
                  confirmButtonText: '',
                  center: true
                })
              }
            })
          }
        })
      },
      // 
      handleSelectionChange(val){
        this.multipleSelection=[]
        val.map((item)=>{
          this.multipleSelection.push(item.uuid)
        })
      },
      // 
      fastDeleteBth(){
        const params={
          tableName: "supplier_contract",
          uuidList: this.multipleSelection
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if(res.status == 0){
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      },
      // 
      deleteBth(scope){
        const params={
          tableName: "supplier_contract",
          uuidList: Array(scope.uuid)
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if(res.status == 0){
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      }
    }
  }
</script>

<style lang='stylus' scoped>
  .contacTitleWarp
    width 100%
    height 50px;
    margin-top 30px;
    background: #f5f5f5;
    position relative
    .contacTitle
      width 110px;
      line-height 50px;
      font-size 20px;
      text-align center
      position absolute
      left 50%;
      margin-left: -65px;
    .contacTitleButton
      position absolute
      right 100px;
      top: 11px;

  .contacTinformation
    width: 100%;
    margin 0px auto
    margin-bottom 10px;

  .addContactsWarp
    width 80%
    margin 0px auto
    .defineWarp
      width 100%
      height: 45px;
      margin-top 20px;
      margin-left 20px;
      display flex
      justify-content center
      align-items center
</style>



