<template>
  <div>
    <!---->
    <div class='contacTitleWarp'>
      <div class='contacTitle'></div>
      <div class='contacTitleButton'>
        <el-button type="info" size="mini" @click='haldAddPmaterial'></el-button>
        <el-button type="danger" size="mini" @click='fastDeleteBth'></el-button>
      </div>
    </div>
    <!---->
    <div class='contacTinformation'>
      <el-table
        :data="materialSourcesList"
        border
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          type="index"
          align='center'
          width="50">
        </el-table-column>
        <el-table-column
          prop="materialName"
          label=""
          align='center'
          width="220">
        </el-table-column>
        <el-table-column
          prop="supplier"
          label=""
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="remark"
          label=""
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          align='center'
          label=""
          width="120">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small" @click='deleteBth(scope.row)'>
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="materialShow" top='40px'>
      <div class='addContactsWarp'>
        <el-form ref="addMaterialData" :model="addMaterialData" :rules='rules' label-width="80px" size='medium'
                 label-position='top'>
          <el-form-item label=":" prop='materialName'>
            <el-input v-model="addMaterialData.materialName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" label-width='120px' prop='supplier'>
            <el-input v-model="addMaterialData.supplier" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" prop='remark'>
            <el-input
              type="textarea"
              :rows="4"
              placeholder=""
              v-model="addMaterialData.remark"
              resize='none'
            >
            </el-input>
          </el-form-item>
        </el-form>
        <!---->
        <div class='defineWarp'>
          <el-button type="primary" size='medium' @click='additions'></el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  // 
  import {config} from 'api/config.js';
  //
  import axios from 'api/axios.js'
  // Vuex
  import {mapState} from 'Vuex'

  export default {

    name: 'materialSources',
    props: {
      materialSourcesList: {
        type: Array
      }
    },
    data() {
      return {
        // 
        materialShow: false,
        // 
        multipleSelection:[],
        // 
        addMaterialData: {
          // 
          materialName: '',
          // 
          supplier: '',
          // 
          remark: ''
        },
        // 
        rules: {
          materialName: [
            {required: true, message: '', trigger: 'blur'},
          ],
          supplier: [
            {required: true, message: '', trigger: 'blur'},
          ],
          remark: [
            {required: true, message: '', trigger: 'blur'},
          ]
        }
      }
    },
    computed: {
      //  uuid
      ...mapState(['uuid']),
    },
    methods: {
      // 
      handleSelectionChange(val){
        this.multipleSelection=[]
        val.map((item)=>{
          this.multipleSelection.push(item.uuid)
        })
      },
      // 
      haldAddPmaterial() {
        this.materialShow = true
        // 
        this.addMaterialData.materialName='',
        this.addMaterialData.supplier='',
        this.addMaterialData.remark=''

      },
      // 
      additions() {
        // 
        this.$refs['addMaterialData'].validate((valid) => {
          if (valid) {
            const params = {
              supplierUuid: this.uuid,
              // 
              materialName: this.addMaterialData.materialName,
              // 
              supplier: this.addMaterialData.supplier,
              // 
              remark: this.addMaterialData.remark
            }
            axios.post(config.api, '/supplier/addSupplierMaterialSource.do', params).then((res) => {
              if (res.status == 0) {
                this.$alert('', {
                  confirmButtonText: '',
                  center: true,
                  callback: action => {
                    // 
                    this.materialShow = false
                    // 
                    this.$emit('againLoad')
                  }
                })
              } else {
                this.$alert(res.msg, {
                  confirmButtonText: '',
                  center: true
                })
              }
            })
          }
        })
      },
      // 
      fastDeleteBth(){
        const params={
          tableName: "supplier_material_source",
          uuidList: this.multipleSelection
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if(res.status == 0){
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      },
      // 
      deleteBth(scope) {
        const params = {
          tableName: "supplier_material_source",
          uuidList: Array(scope.uuid)
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if (res.status == 0) {
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      }
    }
  }
</script>

<style lang='stylus' scoped>
  .contacTitleWarp
    width 100%
    height 50px;
    margin-top 30px;
    background: #f5f5f5;
    position relative
    .contacTitle
      width 130px;
      line-height 50px;
      font-size 20px;
      text-align center
      position absolute
      left 50%;
      margin-left: -65px;
    .contacTitleButton
      position absolute
      right 100px;
      top: 11px;

  .contacTinformation
    width: 100%;
    margin 0px auto
    margin-bottom 10px;

  .addContactsWarp
    width 80%
    margin 0px auto
    .defineWarp
      width 100%
      height: 45px;
      margin-top 20px;
      margin-left 20px;
      display flex
      justify-content center
      align-items center
</style>



