<template>
  <div>
    <div class='contacTitleWarp'>
      <div class='contacTitle'></div>
      <div class='contacTitleButton'>
        <el-button type="info" size="mini" @click='addOtherCertificates'></el-button>
      </div>
    </div>
    <el-table
      :data="OtherCertificates"
      border
      style="width: 100%">
      <el-table-column
        align='center'
        prop="ertificateName"
        label=""
        min-width="180">
      </el-table-column>
      <el-table-column
        align='center'
        prop="ertificateNumber"
        label=""
        min-width="200">
      </el-table-column>
      <el-table-column
        align='center'
        label=""
        min-width='200'
      >
        <template slot-scope="scope">
          <span>{{scope.row.effectiveDate.slice(0,10)}}</span>
        </template>
      </el-table-column>
      <el-table-column
        align='center'
        label=""
        min-width='200'
      >
        <template slot-scope="scope">
          <span>{{scope.row.invalidDate.slice(0,10)}}</span>
        </template>
      </el-table-column>
      <el-table-column
        align='center'
        label=""
        min-width='200'
      >
        <template slot-scope="scope">
          <a :href='scope.row.filePath' download  target="_blank"><el-button size="mini" type="primary"></el-button></a>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="otherCertificatesShow">
      <div class='addContactsWarp'>
        <el-form ref="addCertificateOthen" :model="addCertificateOthen" :rules='rules' label-width="80px" size='medium' label-position='top'>
          <el-form-item label="" prop='ertificateName'>
            <el-input v-model="addCertificateOthen.ertificateName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='ertificateNumber'>
            <el-input v-model="addCertificateOthen.ertificateNumber" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='effectiveDate'>
            <el-date-picker
              v-model="addCertificateOthen.effectiveDate"
              type="date"
              placeholder=""
              value-format='yyyy-MM-dd'
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="" prop='invalidDate'>
            <el-date-picker
              v-model="addCertificateOthen.invalidDate"
              type="date"
              placeholder=""
              value-format='yyyy-MM-dd'
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-upload
              :multiple='false'
              :limit='1'
              class="upload-demo"
              action="http://www.srm-greatech.com/gzSrm/system/file/upFile.do?fileType=qualification.path"
              :on-success="otherFileChange"
              :headers='header'
              :on-remove='removeOtherFile'
              :file-list=[]
            >
              <el-button size="small" type="primary" style='margin-left:30px;margin-top:3px;width: 200px'></el-button>
            </el-upload>
          </el-form-item>
        </el-form>
        <!---->
        <div class='defineWarp'>
          <el-button type="primary" size='medium' @click='additions'></el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  // 
  import {config} from 'api/config.js';
  //
  import axios from 'api/axios.js'
  // Vuex
  import {mapState} from 'Vuex'

  export default {
    name:'addOtherCertificates',
    props: {
      OtherCertificates: {
        type: Array
      }
    },
    data(){
      return{
        // token
        header:{
          Authorization: localStorage.getItem('token')
        },
        // 
        otherCertificatesShow:false,
        // 
        addCertificateOthen:{
          // 
          ertificateName:'',
          // 
          ertificateNumber:'',
          // 
          effectiveDate:'',
          // 
          invalidDate:'',
          // 
          type:5,
          // 
          filePath:''
        },
        // 
        rules: {
          ertificateName: [
            {required: true, message: '', trigger: 'blur'},
          ],
          ertificateNumber:[
            {required: true, message: '', trigger: 'blur'},
          ],
          effectiveDate:[
            {required: true, message: '', trigger: 'blur'},
          ],
          invalidDate:[
            {required: true, message: '', trigger: 'blur'},
          ]
        }
      }
    },
    computed: {
      //  uuid
      ...mapState(['uuid']),
    },
    methods:{
      // 
      addOtherCertificates(){
        this.otherCertificatesShow=true
        // 
        this.addCertificateOthen.ertificateName=''
        this.addCertificateOthen.ertificateNumber=''
        this.addCertificateOthen.effectiveDate=''
        this.addCertificateOthen.invalidDate=''
        this.addCertificateOthen.filePath=''
      },
      // 
      otherFileChange(response){
        this.addCertificateOthen.filePath=response.data[0]
      },
      // 
      removeOtherFile(){
        this.addCertificateOthen.filePath=''
      },
      // 
      additions() {
        // 
        this.$refs['addCertificateOthen'].validate((valid) => {
          if (valid) {
            const params = {
              supplierUuid:this.uuid,
              // 
              ertificateName:this.addCertificateOthen.ertificateName,
              // 
              ertificateNumber:this.addCertificateOthen.ertificateNumber,
              // 
              effectiveDate:this.addCertificateOthen.effectiveDate,
              // 
              invalidDate:this.addCertificateOthen.invalidDate,
              // 
              filePath:this.addCertificateOthen.filePath,
              // 
              type:'5'
            }
            axios.post(config.api, '/supplier/addSupplierCertificate.do', params).then((res) => {
              if (res.status == 0) {
                this.$alert('', {
                  confirmButtonText: '',
                  center: true,
                  callback: action => {
                    // 
                    this.otherCertificatesShow = false
                    // 
                    this.$emit('againLoad')
                  }
                })
              } else {
                this.$alert(res.msg, {
                  confirmButtonText: '',
                  center: true
                })
              }
            })
          }
        })
      },
    }
  }
</script>

<style lang='stylus' scoped>
  .contacTitleWarp
    width 100%
    height 50px;
    margin-top 10px;
    background: #f5f5f5;
    position relative
    .contacTitle
      width 130px;
      line-height 50px;
      font-size 20px;
      text-align center
      position absolute
      left 50%;
      margin-left: -65px;
    .contacTitleButton
      position absolute
      right 100px;
      top: 11px;

  .addContactsWarp
    width 80%
    margin 0px auto
    .defineWarp
      width 100%
      height:45px;
      margin-top 20px;
      margin-left 20px;
      display flex
      justify-content center
      align-items center
</style>



