<template>
  <div>
    <div class='contacTitleWarp'>
      <div class='contacTitle'></div>
      <div class='contacTitleButton'>
        <el-button type="info" size="mini" @click='haldAddBank'></el-button>
        <el-button type="danger" size="mini" @click='fastDeleteBth'></el-button>
      </div>
    </div>
    <!---->
    <div class='contacTinformation'>
      <el-table
        :data="bankList"
        border
        style="width: 100%"
        size='mini'
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          type="index"
          align='center'
          width="50">
        </el-table-column>
        <el-table-column
          prop="accountName"
          label=""
          align='center'
          width="220">
        </el-table-column>
        <el-table-column
          prop="openAccountBankName"
          label=" ()"
          align='center'
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="account"
          label=""
          align='center'
          min-width="90">
        </el-table-column>
        <el-table-column
          prop="bankCode"
          label=""
          align='center'
          width="180">
        </el-table-column>
        <el-table-column
          align='center'
          label=""
          min-width='100'
        >
          <template slot-scope="scope">
            <a :href='scope.row.invoiceDocuments' download target="_blank" style='color:#409EFF;font-size:12px;cursor: pointer' ></a>
          </template>
        </el-table-column>
        <el-table-column
          align='center'
          label=""
          width="120">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small" @click='bankeDit(scope)'>
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="bankAccountShow">
      <div class='addContactsWarp'>
        <el-form ref="addBankData" :model="addBankData" :rules='rules' label-width="80px" size='medium' label-position='top'>
          <el-form-item label="" prop='accountName'>
            <el-input v-model="addBankData.accountName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='openAccountBankName'>
            <el-input v-model="addBankData.openAccountBankName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='account'>
            <el-input v-model="addBankData.account" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='bankCode'>
            <el-input v-model="addBankData.bankCode" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop='invoiceDocuments'>
            <el-upload
              :multiple='false'
              :limit='1'
              class="upload-demo"
              action="http://www.srm-greatech.com/gzSrm/system/file/upFile.do?fileType=qualification.path"
              :on-success="otherFileChange"
              :headers='header'
              :on-remove='removeOtherFile'
              :file-list=[]
            >
              <el-button size="small" type="primary" style='margin-left:30px;margin-top:3px;width: 200px'></el-button>
            </el-upload>
          </el-form-item>
        </el-form>
        <!---->
        <div class='defineWarp'>
          <el-button type="primary" size='medium' @click='additions'></el-button>
        </div>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="bankeDitShow">
      <div class='addContactsWarp'>
        <el-form ref="bankeDit"  :rules='rules' label-width="80px" size='medium' label-position='top'>
          <el-form-item label="">
            <el-upload
              :multiple='false'
              :limit='1'
              class="upload-demo"
              action="http://www.srm-greatech.com/gzSrm/system/file/upFile.do?fileType=qualification.path"
              :on-success="editFileChange"
              :headers='header'
              :on-remove='removeeditFile'
              :file-list=[]
            >
              <el-button size="small" type="primary" style='margin-left:30px;margin-top:3px;width: 200px'></el-button>
            </el-upload>
          </el-form-item>
        </el-form>
        <!---->
        <div class='defineWarp'>
          <el-button type="primary" size='medium' @click='upEdit'></el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  // 
  import {config} from 'api/config.js';
  //
  import axios from 'api/axios.js'
  // Vuex
  import {mapState} from 'Vuex'

  export default {
    name: 'bankAccount',
    props: {
      bankList: {
        type: Array
      }
    },
    data() {
      return {
        // token
        header:{
          Authorization: localStorage.getItem('token')
        },
        // 
        bankAccountShow:false,
        // 
        bankeDitShow:false,
        // 
        multipleSelection: [],
        // 
        addBankData:{
          // 
          accountName: '',
          // 
          openAccountBankName: '',
          // 
          account: '',
          // 
          bankCode: '',
          // 
          invoiceDocuments:''
        },
        // 
        rules: {
          accountName: [
            {required: true, message: '', trigger: 'blur'},
          ],
          openAccountBankName:[
            {required: true, message: '', trigger: 'blur'},
          ],
          account:[
            {required: true, message: '', trigger: 'blur'},
          ],
          bankCode:[
            {required: true, message: '', trigger: 'blur'},
          ],
          invoiceDocuments:[
            {required: true, message: '', trigger: ['change','blur']},
          ]
        },
        // uuid
        editUuid:'',
        // 
        uploadfile:''
      }
    },
    computed: {
      //  uuid
      ...mapState(['uuid']),
    },
    methods:{
      // 
      haldAddBank(){
          this.bankAccountShow=true
          setTimeout(()=>{
            // 
            this.$refs['addBankData'].resetFields()
          },50)
      },
      // 
      bankeDit(scope){
        this.bankeDitShow=true
        this.editUuid=scope.row.uuid
        // 
        this.uploadfile=''
      },
      // 
      handleSelectionChange(val){
        this.multipleSelection=[]
        val.map((item)=>{
          this.multipleSelection.push(item.uuid)
        })
      },
      // 
      additions() {
        // 
        this.$refs['addBankData'].validate((valid) => {
          if (valid) {
            const params = {
              supplierUuid: this.uuid,
              // 
              accountName:this.addBankData.accountName,
              // 
              openAccountBankName: this.addBankData.openAccountBankName,
              // 
              account: this.addBankData.account,
              // 
              bankCode: this.addBankData.bankCode,
              // 
              invoiceDocuments:this.addBankData.invoiceDocuments
            }
            axios.post(config.api, '/supplier/addSupplierBank.do', params).then((res) => {
              if (res.status == 0) {
                this.$alert('', {
                  confirmButtonText: '',
                  center: true,
                  callback: action => {
                    this.bankAccountShow = false
                    // 
                    this.$emit('againLoad')
                  }
                })
              } else {
                this.$alert(res.msg, {
                  confirmButtonText: '',
                  center: true
                })
              }
            })
          }
        })
      },
      // 
      upEdit(){
        const params={
          uuid:this.editUuid,
          invoiceDocuments:this.uploadfile
        }
        axios.post(config.api, '/supplier/editSupplierBank.do', params).then((res) => {
          if (res.status == 0) {
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                this.bankeDitShow = false
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      },
      // 
      fastDeleteBth(){
        const params={
          tableName: "supplier_bank",
          uuidList: this.multipleSelection
        }
        axios.post(config.api, '/supplier/deleteSupplierExtraInformation.do', params).then((res) => {
          if(res.status == 0){
            this.$alert('', {
              confirmButtonText: '',
              center: true,
              callback: action => {
                // 
                this.$emit('againLoad')
              }
            })
          } else {
            this.$alert(res.msg, {
              confirmButtonText: '',
              center: true
            })
          }
        })
      },
      // 
      otherFileChange(response){
        this.addBankData.invoiceDocuments=response.data[0]
        // 
        this.$refs['addBankData'].clearValidate('invoiceDocuments')
      },
      // 
      removeOtherFile(){
        this.addBankData.invoiceDocuments=''
      },
      // 
      editFileChange(response){
        this.uploadfile=response.data[0]
      },
      // 
      removeeditFile(){
        this.uploadfile=''
      }
    }
  }
</script>

<style lang='stylus' scoped>
  .contacTitleWarp
    width 100%
    height 50px;
    margin-top 30px;
    background: #f5f5f5;
    position relative
    .contacTitle
      width 130px;
      line-height 50px;
      font-size 20px;
      text-align center
      position absolute
      left 50%;
      margin-left: -65px;
    .contacTitleButton
      position absolute
      right 100px;
      top: 11px;

  .contacTinformation
    width: 100%;
    margin 0px auto
    margin-bottom 10px;

  .addContactsWarp
    width 80%
    margin 0px auto
    .defineWarp
      width 100%
      height:45px;
      margin-top 20px;
      margin-left 20px;
      display flex
      justify-content center
      align-items center
</style>



